@import "_reset";
@import "_common";
@import "_footer";

.sec_1{
  background: url(../img/bg_01.jpg) no-repeat center top;
  padding: 0 0 1%;
}

.sec_2{
  background: url(../img/bg_02.jpg) no-repeat center top;
  padding: 2% 0;
  .box_video{
    background: url(../img/bg_03.png) no-repeat center;
    max-width: 900px;
    margin: 0 auto;
    max-height: 475px;
    padding-top: 50%;
    .video_wrapper{
      max-width: 850px;
      iframe{
        height: 100%;
      }
    }
  }
}

.sec_3{
  background-color: #000;
  p{
    color: #fff;
    padding-left: 2.5em;
    font-size: 20px;
    &.t_sml{
      font-size: 90%;
      padding-left: 3.5em;
    }
    &.more{
      margin-top: 2em;
    }
  }
  @media screen and (max-width:768px) {
    p{
      padding-left: 0;
      &.t_sml{
        padding-left: 0;
      }
    }
  }
}

.more{
  a{
    background-color: #22ac38;
    color: #fff;
    max-width: 360px;
    text-align: center;
    border-radius: 50px;
    padding: 1em;
  }
}

.box_1{
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 5%;
  max-width: 900px;
  figure{
    width: auto;
    margin: 0 5px;
  }
}

.sec_4{
  background: url(../img/bg_04.jpg) no-repeat center top;
  background-size: cover;
  padding: 2% 0;
  p{
    color: #fff;
  }
  @media screen and (max-width:768px) {
    p.t_xlrg{
      font-size: 130%;
    }
  }
}

.sec_5{
  background: url(../img/bg_05.jpg) no-repeat center top;
  background-size: cover;
  padding: 2% 0;
  p{
    color: #fff;
  }
  .box_2 .item figure figcaption{
    background-color: #c6120b;
  }
  .box_2.item_a .item .txt .inn{
    background-color: #c6120b;
    p{
      &.txt_1{
        color: #000;
        font-weight: 700;
      }
      &.txt_2{
        color: #fff;
      }
      &.txt_3{
        color: #fff;
      }
      &:last-child{
        color: #fff;
      }
    }
  }
  @media screen and (max-width:768px) {
    p.t_xlrg{
      font-size: 130%;
    }
  }
}

.box_2{
  max-width: 980px;
  margin: 0 auto 2%;
  .item{
    background-color: #000;
    max-width: 222px;
    figure{
      margin-bottom: 0;
      position: relative;
      figcaption{
        display: block;
        background-color: #1b1b1b;
        color: #fff;
        font-size: 27px;
        width: 2.5em;
        height: 2em;
        right: 0;
        bottom: 0;
        position: absolute;
        line-height: 2em;
        font-weight: 900;
        @include fm();
        span{
          font-size: 45px;
        }
      }
    }
    .txt{
      position: relative;
      padding: 5%;
      p{
        margin-bottom: 0;
        color: #fff;
        &.txt_1{
          font-size: 17px;
        }
        &.txt_2{
          font-size: 13px;
        }
        &.txt_3{
          font-size: 21px;
          margin-bottom: 1.5em;
        }
        &:last-child{
          position: absolute;
          font-size: 13px;
          right: 5%;
          bottom: 5%;
          a{
            color: #fff;
          }
        }
      }
    }
  }
  &.item_a .item{
    display: flex;
    max-width: 100%;
    figure{
      width: 28%;
      figcaption{
        font-size: 34px;
        span{
          font-size: 55px;
        }
      }
    }
    .txt{
      background: url(../img/bg_06.jpg) repeat-y center top;
      padding: 4px;
      width: 72%;
      display: flex;
      .inn{
        background-color: #000;
        padding: 2%;
        width: 100%;
        p{
          &.txt_1{
            font-size: 23px;
            color: #c6120b;
          }
          &.txt_2{
            font-size: 18px;
          }
          &.txt_3{
            font-size: 29px;
          }
          &:last-child{
            font-size: 18px;
            right: 3%;
          }
        }
      }
    }
  }
  @media screen and (max-width:768px) {
    margin-bottom: 0 !important;
    .item{
      margin-bottom: 10px !important;
      figure figcaption{
        font-size: 4.7vw;
        span{
          font-size: 8vw;
        }
      }
      .txt{
        p{
          &.txt_1{
            font-size: 3.7vw;
          }
          &.txt_2{
            font-size: 3.2vw;
          }
          &.txt_3{
            font-size: 4.7vw;
          }
          &:last-child{
            right: 7%;
            font-size: 3.2vw;
          }
        }
      }
    }
    &.item_a .item{
      figure{
        width: 40%;
        figcaption{
          font-size: 6.7vw;
          span{
            font-size: 10vw;
          }
        }
      }
      .txt{
        width: 60%;
        padding: 3px;
        .inn{
          padding: 4%;
          p{
            &.txt_1{
              font-size: 4.2vw;
            }
            &.txt_2{
              font-size: 3.7vw;
            }
            &.txt_3{
              font-size: 5.2vw;
            }
            &:last-child{
              right: 7%;
              font-size: 3.7vw;
            }
          }
        }
      }
    }
  }
}

footer{
  background-color: #000;
  color: #fff;
  a{
    color: #fff;
  }
  @media screen and (max-width:768px) {
    font-size: 3.2vw;
  }
}