@import url(https://fonts.googleapis.com/earlyaccess/notosansjp.css);

@mixin fs($p){font-size: #{$p}px; font-size: #{( $p / 10 )}rem;}
@mixin fm(){font-family: "游明朝", YuMincho, "ヒラギノ明朝 ProN W3", "Hiragino Mincho ProN", "HG明朝E", "ＭＳ Ｐ明朝", "ＭＳ 明朝", serif;}
@mixin fg(){font-family: 'Noto Sans JP', '游ゴシック Medium', '游ゴシック体', 'Yu Gothic Medium', YuGothic, 'ヒラギノ角ゴ ProN', 'Hiragino Kaku Gothic ProN', 'メイリオ', Meiryo, 'ＭＳ Ｐゴシック', 'MS PGothic', sans-serif;}

@media screen and (max-width:768px) {.pc{display: none !important;} .sp{display: block !important;}}
@media screen and (min-width:769px) {.sp{display: none !important;} .pc{display: block !important;}}

html{
	font-size: 62.5%;
}
body {
	line-height:1;
	@include fs(16);
	@include fg();
	color: #333;
	-webkit-text-size-adjust: 100%;
	max-width: 1920px;
  margin: 0 auto;
  @media screen and (max-width:768px) {
    font-size: 4.2vw;
  }
}

section{
  padding: 70px 0;
  .s_hdr{
    display: flex;
    width: 100%;
    align-items: center;
  }
  .inner{
    overflow: hidden;
    width: 100%;
    max-width: 1120px;
    margin: 0 auto;
    padding: 0 70px;
    &:after {
      content: "";
      display: block;
      clear: both;
    }
	}
  @media screen and (max-width:768px) {
    padding: 8% 0;
    .inner{
      padding: 0 5%;
    }
  }
}
figure{
  width: 100%;
  margin: 0 auto 40px;
  text-align: center;
  line-height: 0;
  img{
    max-width: 100%;
    margin: 0 auto;
    line-height: 1;
  }
  &:last-child{
    margin-bottom: 0;
  }
  @media screen and (max-width:768px) {
    margin-bottom: 3%;
  }
}
p{
  @include fs(18);
  margin-bottom: 1.5em;
  line-height: 1.65em;
  &:last-child{
    margin-bottom: 0;
  }
  @media screen and (max-width:768px) {
    /*font-size: 3.7vw; 14px*/
    font-size: 4.2vw; /*16px*/
    /*font-size: 4.8vw; 18px*/
    /*font-size: 5.3vw; 20px*/
  }
}

.box_video{
	position: relative;
	width: 100%;
	height: 56.25%;
	padding: 56.25% 0 0 0;
	text-align: center;
	.video_wrapper{
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    height: 100%;
    margin: 0 auto;
		iframe{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
		}
	}
}

.ripple{
	position: relative;
	display: block;
	img{
		max-width: 100%;
		margin: 0 auto;
	}
	span{
		position: absolute;
		z-index: 1;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		display: block;
		width: 100%;
		height: 100%;
		margin: auto;
		animation-name: rippleAnimation;
		animation-duration: 2s;
		animation-timing-function: ease-out;
		animation-iteration-count: infinite;
		border-radius: 100px;
		background-color: #22ac38;
		box-shadow: 0 0 5px white;
	}
}
@media screen and (max-width:768px) {
	span{
    width: 100%;
  }
}
@-webkit-keyframes rippleAnimation {
  0% {
    transform: scale(1, 1);
    opacity: .5;
  }

  100% {
    transform: scale(1.1, 1.1);
    opacity: 0;
  }
}
@keyframes rippleAnimation {
  0% {
    transform: scale(1, 1);
    opacity: .5;
  }

  100% {
    transform: scale(1.1, 1.1);
    opacity: 0;
  }
}

.ripple2{
  position: relative;
  display: block;
	img{
    	max-width: 100%;
		margin: 0 auto;
	}
	span{
		&.clr_1, &.clr_2{
	    position: absolute;
	    z-index: 1;
	    top: 0;
	    right: 0;
	    bottom: 0;
	    left: 0;
	    display: block;
	    width: 70px;
	    height: 70px;
	    margin: auto;
      animation-name: rippleAnimation2;
      animation-duration: 2s;
      animation-timing-function: ease-out;
      animation-iteration-count: infinite;
	    border-radius: 100%;
	    box-shadow: 0 0 45px 1px #ff004a inset;
		}
		&.clr_2{
		  -webkit-animation-delay: .3s;
		          animation-delay: .3s;
		}
	}
  @media screen and (max-width:768px) {
    span{
      &.clr_1, &.clr_2{
        width: 30px;
	      height: 30px;
      }
    }
  }
}
@-webkit-keyframes rippleAnimation2{
  0% {
      transform: scale(1,1);
      opacity: 1;
  }
  50% {
      opacity: 0.6;
  }
  80% {
      transform: scale(4.5,4.5);
      opacity: 0;
  }
  100% {
      transform: scale(4.5,4.5);
      opacity: 0;
  }
}
@keyframes rippleAnimation2{
  0% {
      transform: scale(1,1);
      opacity: 1;
  }
  50% {
      opacity: 0.6;
  }
  80% {
      transform: scale(4.5,4.5);
      opacity: 0;
  }
  100% {
      transform: scale(4.5,4.5);
      opacity: 0;
  }
}

.bounce{
	position: relative;
	&::after{
		position: absolute;
		right: 0;
		bottom: 0;
		left: 0;
		display: block;
		width: 100px;
		height: 50px;
		margin: 0 auto auto;
		content: '';
		background: url(../img/arw_1.png) no-repeat center top;
		animation-name: bounce;
		animation-duration: 2s;
		animation-timing-function: ease-out;
		animation-iteration-count: infinite;
	}
  @media screen and (max-width:768px) {
    &::after{
      width: 40px;
      height: 25px;
      background-size: cover;
    }
  }
}

.faq{
  background-color: #fff;
  padding: 4%;
  line-height: 1.65em;
  margin-top: 5%;
  font-size: 18px;
  dt{
    font-weight: bold;
    padding: .3em 0 0 3em;
    background: url(../img/icon_q.png) no-repeat left top;
    margin-bottom: 2em;
    min-height: 62px;
    border-bottom: #ccc dashed 1px;
  }
  dd{
    padding: .3em 0 0 3em;
    background: url(../img/icon_a.png) no-repeat left top;
    margin-bottom: 2em;
    min-height: 42px;
    &:last-of-type{
      margin-bottom: 0;
    }
  }
  @media screen and (max-width:768px) {
    font-size: 4.2vw;
    dt{
      background-size: 35px auto;
      background-position: left 7px;
      padding: .8em 0 .5em 3em;
      margin-bottom: 1.3em;
    }
    dd{
      background-size: 35px auto;
    }
  }
}

.hand{
  position: relative;
  &::after{
    position: absolute;
    display: block;
    content: '';
    width: 132px;
    height: 117px;
    background: url(../img/hand.png) no-repeat;
    top: 0;
    right: 0;
    bottom: -50px;
    left: 0;
    margin: auto 0 0 auto;
    animation: shakeY;
    animation-duration: 4s;
    animation-iteration-count: infinite;
  }
  @media screen and (max-width:768px) {
    &::after{
      width: 45px;
      height: 42px;
      background-size: 100% auto;
      bottom: -20px;
    }
  }
}

.Fade-In {
  opacity: 0;
  transform: translateY(20px);
  transition: all 1s;
  &.Fade-In-2 {
    transition-delay: 0.2s;
  }
  &.Fade-In-3 {
    transition-delay: 0.4s;
  }
  &.Fade-In-4 {
    transition-delay: 0.6s;
  }
}

.Fade-In-Down {
  opacity: 0;
  transform: translateY(-20px);
  transition: all 1s;
  &.Fade-In-Down-2 {
    transition-delay: 0.2s;
  }
  &.Fade-In-Down-3 {
    transition-delay: 0.4s;
  }
  &.Fade-In-Down-4 {
    transition-delay: 0.6s;
  }
}

.Fade-In-Left {
  opacity: 0;
  transform: translateX(-20px);
  transition: all 1s;
  &.Fade-In-Left-2 {
    transition-delay: 0.2s;
  }
  &.Fade-In-Left-3 {
    transition-delay: 0.4s;
  }
  &.Fade-In-Left-4 {
    transition-delay: 0.6s;
  }
}

.Fade-In-Right {
  opacity: 0;
  transform: translateX(20px);
  transition: all 1s;
  &.Fade-In-Right-2 {
    transition-delay: 0.2s;
  }
  &.Fade-In-Right-3 {
    transition-delay: 0.4s;
  }
  &.Fade-In-Right-4 {
    transition-delay: 0.6s;
  }
}

